class Command {
    name = '';
    constructor(verse) {
        this.verse = verse;
    }
    execute() { }
}

class CountWords extends Command {
    name = 'CountWords';
    execute() {
        return this.verse.length % 7 == 0;
    }
}
class CountLetters extends Command {
    constructor(verse, mod) {
        super(verse);
        this.mod = mod;
    }
    name = 'CountLetters';
    execute() {
        let letterCount = 0;
        for (var i = 0; i < this.verse.length; i++) {
            letterCount += this.verse[i].clean.length;
        }
        return letterCount % this.mod == 0;
    }
}
class Misparim {
    searchText(arr) {
        let resultArr = [];
        arr.forEach(letter => {
            let obj = this.letters.find(o => o.letter === letter);
            !obj ? false : resultArr.push(obj.number);

        });
        return resultArr;
    }

    letters = [
        { letter: '', number: 1 },
        { letter: '', number: 2 },
        { letter: '', number: 3 },
        { letter: '', number: 4 },
        { letter: '', number: 5 },
        { letter: '', number: 6 },
        { letter: '', number: 7 },
        { letter: '', number: 8 },
        { letter: '', number: 9 },
        { letter: '', number: 10 },
        { letter: '', number: 20 },
        { letter: '', number: 20 },
        { letter: '', number: 30 },
        { letter: '', number: 40 },
        { letter: '', number: 40 },
        { letter: '', number: 50 },
        { letter: '', number: 50 },
        { letter: '', number: 60 },
        { letter: '', number: 70 },
        { letter: '', number: 80 },
        { letter: '', number: 80 },
        { letter: '', number: 90 },
        { letter: '', number: 90 },
        { letter: '', number: 100 },
        { letter: '', number: 200 },
        { letter: '', number: 300 },
        { letter: '', number: 400 }
    ];

}
class MisparimVerse extends Command {
    name = 'MisparimVerse';
    misparimHelper = new MisparimHelper();
    divisor = 0;
    constructor(verse, divisor) {
        super(verse);
        this.divisor = divisor;
    }
    execute() {
        let resultArr = this.misparimHelper.generateMisparim(this.verse);
        let misparim = resultArr.reduce((a, b) => a + b);
        return misparim % this.divisor == 0;
    }
}
class MisparimHelper {
    generateMisparimArray(verse) {
        let inputArray = [];
        for (let i = 0; i < verse.length; i++) {
            for (let j = 0; j < verse[i].clean.length; j++) {
                inputArray.push(verse[i].clean[j]);
            }
        }
        return inputArray;
    }
    generateMisparim(verse) {
        let misparim = new Misparim();
        let inputArray = this.generateMisparimArray(verse);
        return misparim.searchText(inputArray);
    }
    generateMisparimByType(verse, type) {
        let misparim = new Misparim();
        let inputArray = [];
        for (let i = 0; i < verse.length; i++) {
            for (let j = 0; j < verse[i].morphology.length; j++) {
                if (verse[i].morphology[j].function == type) {
                    for (let k = 0; k < verse[i].clean.length; k++) {
                        inputArray.push(verse[i].clean[k]);
                    }
                }
            }
        }
        let resultArr = misparim.searchText(inputArray);
        return resultArr.reduce((a, b) => a + b);
    }
}
class MisparimNouns extends Command {
    name = 'MisparimNouns';
    misparimHelper = new MisparimHelper();
    execute() {
        let misparim =
            this.misparimHelper.generateMisparimByType(this.verse, 'Noun');
        return misparim % 7 == 0;
    }
}
class MisparimVerbs extends Command {
    name = 'MisparimVerbs';
    misparimHelper = new MisparimHelper();
    execute() {
        let misparim = this.misparimHelper.generateMisparimByType(this.verse, 'Verb');
        return misparim % 7 == 0;
    }
}
class MisparimFirstLastMiddle extends Command {
    name = 'MisparimFirstLastMiddle';
    misparimHelper = new MisparimHelper();
    misparim = new Misparim();
    execute() {
        let inputArray = this.misparimHelper.generateMisparimArray(this.verse);
        let firstLastMiddle = [];
        firstLastMiddle.push(inputArray[0]);
        firstLastMiddle.push(inputArray[inputArray.length - 1]);
        if (inputArray.length % 2 == 0) {
            firstLastMiddle.push(inputArray[(inputArray.length / 2) - 1]);
            firstLastMiddle.push(inputArray[(inputArray.length / 2)]);
        } else {
            firstLastMiddle.push(inputArray[Math.floor(inputArray.length / 2)]);
        }
        let resultArr = this.misparim.searchText(firstLastMiddle);
        return resultArr.reduce((a, b) => a + b) % 7 == 0;
    }
}
class MisparimFirstLastOfEachWord extends Command {
    name = 'MisparimFirstLastOfEachWord';
    misparimHelper = new MisparimHelper();
    misparim = new Misparim();
    execute() {
        let firstLast = [];
        for (var i = 0; i < this.verse.length; i++) {
            firstLast.push(this.verse[i].clean[0]);
            firstLast.push(this.verse[i].clean[this.verse[i].clean.length - 1]);
        }
        let resultArr = this.misparim.searchText(firstLast);
        let result = resultArr.reduce((a, b) => a + b);
        return result % 7 == 0;
    }
}
class MisparimFirstLastOfFirstAndLast extends Command {
    name = 'MisparimFirstLastOfFirstAndLast';
    misparimHelper = new MisparimHelper();
    misparim = new Misparim();
    execute() {
        let firstLast = [];
        //first word
        let clean = this.verse[0].clean;
        firstLast.push(clean[0]);
        firstLast.push(clean[clean.length - 1]);

        //last word
        clean = this.verse[this.verse.length - 1].clean;
        firstLast.push(clean[0]);
        firstLast.push(clean[clean.length - 1]);

        let resultArr = this.misparim.searchText(firstLast);
        let result = resultArr.reduce((a, b) => a + b);
        return result % 7 == 0;
    }
}

class MisparimFirstLastBetweenFirstAndLast extends Command {
    name = 'MisparimFirstLastBetweenFirstAndLast';
    misparimHelper = new MisparimHelper();
    misparim = new Misparim();
    execute() {
        let firstLast = [];
        for (var i = 1; i < this.verse.length - 1; i++) {
            let clean = this.verse[i].clean;
            firstLast.push(clean[0]);
            firstLast.push(clean[clean.length - 1]);
        }
        let resultArr = this.misparim.searchText(firstLast);
        let result = resultArr.reduce((a, b) => a + b);
        return result % 7 == 0;
    }
}
class MisparimOfEvenNumberOfLetters extends Command {
    name = 'MisparimOfEvenNumberOfLetters';
    misparimHelper = new MisparimHelper();
    misparim = new Misparim();
    execute() {
        let even = [];
        for (var i = 0; i < this.verse.length; i++) {
            let clean = this.verse[i].clean;
            if (clean.length % 2 == 0) {
                for (var j = 0; j < clean.length; j++) {
                    even.push(clean[j]);
                }
            }
        }
        let resultArr = this.misparim.searchText(even);
        let result = resultArr.reduce((a, b) => a + b);
        return result % 7 == 0;
    }
}
class CountOfFirstTwoAndLastTwo extends Command {
    name = 'CountOfFirstTwoAndLastTwo';
    execute() {
        let firstLast = 0;
        firstLast += this.verse[0].clean.length;
        firstLast += this.verse[1].clean.length;
        firstLast += this.verse[this.verse.length - 1].clean.length;
        firstLast += this.verse[this.verse.length - 2].clean.length;
        return firstLast % 4 == 0;
    }
}
class CountBetweenFirstTwoAndLastTwo extends Command {
    name = 'CountBetweenFirstTwoAndLastTwo';
    execute() {
        let firstLast = 0;
        for (let i = 2; i < this.verse.length - 2; i++) {
            firstLast += this.verse[i].clean.length;
        }
        return firstLast % 4 == 0;
    }
}
class CountOfLettersInWord {
    getCounts(verse) {
        let dups = [];
        for (let i = 0; i < verse.length; i++) {
            dups.push(verse[i].clean.length);
        }
        const count = {};
        dups.forEach(element => {
            count[element] = (count[element] || 0) + 1;
        });
        return count;
    }
}
class SumOfCountOfLettersThatAreDuplicate extends Command {
    name = 'SumCountOfLettersThatAreDuplicate';
    execute() {
        let countOfLettersInWord = new CountOfLettersInWord();
        let total = 0;
        let count = countOfLettersInWord.getCounts(this.verse);
        for (const property in count) {
            if (count[property] > 1) {
                total += property * count[property];
            }
        }
        return total % 4 == 0;
    }
}
class SumOfCountOfLettersThatAreNotDuplicate extends Command {
    name = 'SumCountOfLettersThatAreNotDuplicate';
    execute() {
        let countOfLettersInWord = new CountOfLettersInWord();
        let total = 0;
        let count = countOfLettersInWord.getCounts(this.verse);
        for (const property in count) {
            if (count[property] == 1) {
                total += parseInt(property);
            }
        }
        return total % 4 == 0;
    }
}
class MisparimValueSmallestLargestTotal {
    misparim = new Misparim();
    getValues(verse) {

        let firstLast = [];
        let smallest = Number.MAX_SAFE_INTEGER;
        let largest = 0;
        let total = 0;
        for (var i = 0; i < verse.length; i++) {
            firstLast = verse[i].clean.split('');
            let resultArr = this.misparim.searchText(firstLast);
            let result = resultArr.reduce((a, b) => a + b);
            total += result;
            if (result < smallest) {
                smallest = result;
            }
            if (result > largest) {
                largest = result;
            }
        }
        return {
            total: total,
            smallest: smallest,
            largest: largest
        };
    }
}
class MisparimValueOfLargestAndSmallestVerse extends Command {
    name = 'MisparimValueOfLargestAndSmallestVerse';
    misparimValueSmallestLargestTotal = new MisparimValueSmallestLargestTotal();
    execute() {
        let obj = this.misparimValueSmallestLargestTotal.getValues(this.verse);
        return (obj.largest + obj.smallest) % 37 == 0;
    }
}
class MisparimValueOfThoseNotLargestAndSmallestVerse extends Command {
    name = 'MisparimValueOfThoseNotLargestAndSmallestVerse';
    misparimValueSmallestLargestTotal = new MisparimValueSmallestLargestTotal();

    execute() {
        let obj = this.misparimValueSmallestLargestTotal.getValues(this.verse);
        return (obj.total - (obj.largest + obj.smallest)) % 37 == 0;
    }
}
class SumOfMisparimValueOfThoseNotLargestAndSmallestVerse extends Command {
    name = 'SumOfMisparimValueOfThoseNotLargestAndSmallestVerse';
    misparimValueSmallestLargestTotal = new MisparimValueSmallestLargestTotal();

    execute() {
        let obj = this.misparimValueSmallestLargestTotal.getValues(this.verse);
        let sumOfSmallestAndLargest = obj.largest + obj.smallest;
        let sumOfTheRest = obj.total - sumOfSmallestAndLargest;
        let sumSmallLarge = sumOfSmallestAndLargest.toString();
        let sumRest = sumOfTheRest.toString();
        let sumSmallLargeArr = sumSmallLarge.split('');
        let sumRestArr = sumRest.split('');
        let total = 0;
        for (let i = 0; i < sumSmallLargeArr.length; i++) {
            total += parseInt(sumSmallLargeArr[i]);
        }
        for (let i = 0; i < sumRestArr.length; i++) {
            total += parseInt(sumRestArr[i]);
        }
        return total % 37 == 0;
    }
}

export default class Controller {
    commands = [];
    addCommand(obj) {
        this.commands.push(obj);
    }
    execute() {
        for (let i = 0; i < this.commands.length; i++) {
            let result = false;
            try {
                result = this.commands[i].execute();
            } catch (e) {
                result = false;
            } finally {
                this.commands[i].verified = result;
            }
        }
    }
    constructor(res) {
        this.addCommand(new CountLetters(res.data, 7));
        this.addCommand(new CountWords(res.data));
        this.addCommand(new MisparimVerse(res.data, 7));
        this.addCommand(new MisparimNouns(res.data));
        this.addCommand(new MisparimVerbs(res.data));
        this.addCommand(new MisparimFirstLastMiddle(res.data));
        this.addCommand(new MisparimFirstLastOfEachWord(res.data));
        this.addCommand(new MisparimFirstLastOfFirstAndLast(res.data));
        this.addCommand(new MisparimFirstLastBetweenFirstAndLast(res.data));
        this.addCommand(new MisparimOfEvenNumberOfLetters(res.data));
        this.addCommand(new CountLetters(res.data, 4));
        this.addCommand(new CountOfFirstTwoAndLastTwo(res.data));
        this.addCommand(new CountBetweenFirstTwoAndLastTwo(res.data));
        this.addCommand(new SumOfCountOfLettersThatAreDuplicate(res.data));
        this.addCommand(new SumOfCountOfLettersThatAreNotDuplicate(res.data));
        this.addCommand(new MisparimVerse(res.data, 37));
        this.addCommand(new MisparimValueOfLargestAndSmallestVerse(res.data));
        this.addCommand(new MisparimValueOfThoseNotLargestAndSmallestVerse(res.data));
        this.addCommand(new SumOfMisparimValueOfThoseNotLargestAndSmallestVerse(res.data)); this
    }
}

