<template>
  <Toolbar>
    <template #start>
      <div
        class="flex flex-wrap align-items-center justify-content-center card-container"
      >
        <div class="rem m-1 border-round">
          <div
            class="mx-1 border-round text-gray-900 font-bold p-1 flex align-items-center justify-content-center"
          >
            Book
          </div>
        </div>
      </div>
      <CascadeSelect
        v-model="selectedBook"
        :options="covenants"
        optionLabel="cname"
        optionGroupLabel="name"
        :optionGroupChildren="['books']"
        style="minwidth: 14rem"
        placeholder="Book"
        @change="changeBook()"
      >
      </CascadeSelect>
      <div
        class="flex flex-wrap align-items-center justify-content-center card-container"
      >
        <div class="rem m-1 border-round">
          <div
            class="mx-1 border-round text-gray-900 font-bold p-1 flex align-items-center justify-content-center"
          >
            Chapter
          </div>
        </div>
      </div>
      <Dropdown
        v-model="selectedChapter"
        :options="chapters"
        optionLabel="name"
        placeholder="Chapter"
        @change="changeChapter()"
      />
      <div
        class="flex flex-wrap align-items-center justify-content-center card-container"
      >
        <div class="rem m-1 border-round">
          <div
            class="mx-1 border-round text-gray-900 font-bold p-1 flex align-items-center justify-content-center"
          >
            Verse
          </div>
        </div>
      </div>
      <Dropdown
        v-model="selectedVerse"
        :options="verses"
        optionLabel="name"
        placeholder="Verse"
        @change="changeVerse()"
      />
      <div
        class="flex flex-wrap align-items-center justify-content-center card-container"
      >
        <div class="rem m-1 border-round">
          <div
            class="mx-1 border-round text-gray-900 font-bold p-1 flex align-items-center justify-content-center"
          >
            Thru
          </div>
        </div>
      </div>
      <Dropdown
        v-model="selectedThruVerse"
        :options="thruVerses"
        optionLabel="name"
        placeholder="# Verses"
        @change="changeThruVerse()"
      />
    </template>
  </Toolbar>
</template>

<script>
import EventBus from "./AppEventBus";
export default {
  methods: {
    changeThruVerse() {
      EventBus.emit("loadBible", {
        book: this.selectedBook,
        chapter: this.selectedChapter,
        verse: this.selectedVerse,
        thruVerse: this.selectedThruVerse,
      });
    },
    changeVerse() {
      let that = this;
      fetch("data/bible.json", { headers: { "Cache-Control": "no-cache" } })
        .then((res) => res.json())
        .then((d) => {
          for (var i = 0; i < d.length; i++) {
            if (d[i].book == that.selectedBook.cname) {
              for (var j = 0; j < d[i].chapters.length; j++) {
                if (d[i].chapters[j].chapter == that.selectedChapter.name) {
                  that.thruVerses = [];
                  for (
                    var x = that.selectedVerse.code;
                    x <= parseInt(d[i].chapters[j].verses);
                    x++
                  ) {
                    that.thruVerses.push({
                      name: `${parseInt(x)}`,
                      code: `${parseInt(x)}`,
                    });
                  }
                  break;
                }
              }
              break;
            }
          }
        });
    },
    changeBook() {
      fetch("data/bible.json", { headers: { "Cache-Control": "no-cache" } })
        .then((res) => res.json())
        .then((d) => {
          for (var i = 0; i < d.length; i++) {
            if (d[i].book == this.selectedBook.code) {
              this.chapters = [];
              for (var j = 0; j < d[i].chapters.length; j++) {
                this.chapters.push({ name: `${j + 1}`, code: `${j + 1}` });
              }
              break;
            }
          }
        });
    },
    changeChapter() {
      fetch("data/bible.json", { headers: { "Cache-Control": "no-cache" } })
        .then((res) => res.json())
        .then((d) => {
          for (var i = 0; i < d.length; i++) {
            if (d[i].book == this.selectedBook.cname) {
              for (var j = 0; j < d[i].chapters.length; j++) {
                if (d[i].chapters[j].chapter == this.selectedChapter.name) {
                  this.verses = [];
                  for (var x = 0; x < d[i].chapters[j].verses; x++) {
                    this.verses.push({ name: `${x + 1}`, code: `${x + 1}` });
                  }
                  this.showVerses = true;
                  break;
                }
              }
              break;
            }
          }
        });
    },
    onMenuToggle(event) {
      this.$emit("menu-toggle", event);
    },
    onTopbarMenuToggle(event) {
      this.$emit("topbar-menu-toggle", event);
    },
    topbarImage() {
      return this.$appState.darkTheme ? "images/logo-white.svg" : "images/logo-dark.svg";
    },
  },
  created() {
    this.changeBook();
    this.changeChapter();
    this.changeVerse();
    this.changeThruVerse();
  },
  mounted() {
    let that = this;
    setTimeout(function () {
      EventBus.emit("loadBible", {
        book: that.selectedBook,
        chapter: that.selectedChapter,
        verse: that.selectedVerse,
        thruVerse: that.selectedThruVerse,
      });
    }, 500);
  },
  computed: {
    darkTheme() {
      return this.$appState.darkTheme;
    },
  },
  data() {
    return {
      selectedBook: { cname: "Genesis", code: "Gen" },
      selectedChapter: { name: "1", code: "1" },
      selectedVerse: { name: "1", code: "1" },
      selectedThruVerse: { name: "1", code: "1" },
      covenants: [
        {
          name: "Tanakh",
          code: "AU",
          books: [
            { cname: "Genesis", code: "Gen" },
            { cname: "Exodus", code: "Exo" },
            { cname: "Leviticus", code: "Lev" },
            { cname: "Numbers", code: "Num" },
            { cname: "Deuteronomy", code: "Deu" },
          ],
        },
      ],
      chapters: [],
      verses: [],
      thruVerses: [],
    };
  },
};
</script>
