export default class BibleService {
    getMorphology(db, code) {
        let _data = db.exec(
            `SELECT * FROM tehmc 
    where code = ?`,
            [code]);
        let res = {};
        res.data = this.json_extension(_data);
        return res;
    }
    getLexicon(db, strong) {
        let _data = db.exec(
            `SELECT * FROM tbesh 
    where strong = ?`,
            [strong]);
        let res = {};
        res.data = this.json_extension(_data);
        return res;

    }
    getBookChapVerse(db, book, chapter, verse, thruVerse) {
        let _data = db.exec(
            `SELECT * FROM totht 
    where book = ? 
    and chapter >= ? 
    and chapter <= ?
    and verse >= ? 
    and verse <= ? 
    order by 
    book ASC, 
    chapter ASC, 
    verse ASC, 
    word ASC`,
            [book, chapter, chapter, verse, thruVerse]);
        let res = {};
        res.data = this.json_extension(_data);
        return res;

    }

    json_extension(_records) {
        var _json = [];
        var _columns = _records[0].columns
        var _values = _records[0].values
        for (var i = 0; i < _values.length; i++) {
            var _row_json = {};
            var _row = _values[i];
            for (var k = 0; k < _row.length; k++) {
                _row_json[_columns[k]] = _row[k]
            }
            _json.push(_row_json)
        }
        return _json
    }
    async getBible(db, book, chapter, verse, thruVerse) {
        let res = this.getBookChapVerse(db, book, chapter, verse, thruVerse);
        for (let i = 0; i < res.data.length; i++) {
            let morph = res.data[i].morph.split('/');
            res.data[i].morphology = [];
            for (let j = 0; j < morph.length; j++) {
                let morphCode = morph[j];
                if (!morphCode.startsWith('H')) {
                    morphCode = 'H' + morphCode;
                }
                let _data = this.getMorphology(db, `${morphCode}`);
                res.data[i].morphology.push(_data.data[0]);
            }
            let strongs = res.data[i].strongs;
            //['H9003==in', 'H7225==first_1_beginning']
            strongs = strongs.replace("God_God@Gen.1.1", "God");
            strongs = strongs.replace("obj.", "the");
            let stuff = strongs.split("/");
            let english = "";
            let translit = "";
            res.data[i].lexicon = [];
            for (var j = 0; j < stuff.length; j++) {
                //['H9003', '', 'in']
                let words = stuff[j].split("=");
                let strongId = words[0];
                let tbesh = this.getLexicon(db, strongId);
                res.data[i].lexicon.push(tbesh.data[0]);
                translit += tbesh.data[0].translit + ".";
                english += words[2].replace(/[_]+/g, "").replace(/[0-9]/g, " ") + " ";
            }
            //remove ending "."
            res.data[i].translit = translit.slice(0, -1);
            res.data[i].english = english.replace("verseEnd", "").trim();
            res.data[i].clean = res.data[i].hebrew_pointed.replace(/[\u0591-\u05C7]/g, "").trim();
        }
        return res;

    }
}
