<template>
  <div class="grid">
    <div class="col-12">
      <div class="card">
        <Toolbar class="mb-2">
          <template #start>
            <h5>Scripture</h5>
          </template>

          <template #end>
            <button
              class="p-link layout-menu-button layout-topbar-button"
              @click="onMenuToggle"
            >
              <i class="pi pi-bars"></i>
            </button>
          </template>
        </Toolbar>

        <DataView
          v-if="showScripture"
          :value="dataviewValue"
          :layout="layout"
          :rows="5"
          :sortOrder="sortOrder"
          :sortField="sortField"
        >
          <template #grid="slotProps">
            <div class="col-12 md:col-2">
              <div class="card">
                <div class="text-center overflow-scroll">
                  <div class="text-2xl font-bold">{{ slotProps.data.clean }}</div>
                  <div class="text-sm">{{ slotProps.data.english }}</div>
                  <div class="text-sm font-italic">{{ slotProps.data.translit }}</div>
                </div>
              </div>
            </div>
          </template>
        </DataView>
      </div>
    </div>
  </div>

  <div class="grid">
    <div class="col-12">
      <div class="card">
        <Toolbar class="mb-2">
          <template #start>
            <h5>Numerics</h5>
          </template>

          <template #end>
            <button
              class="p-link layout-menu-button layout-topbar-button"
              @click="onMenuNumericsToggle"
            >
              <i class="pi pi-bars"></i>
            </button>
          </template>
        </Toolbar>
        <DataTable
          v-if="showNumerics"
          :value="customer1"
          :paginator="true"
          class="p-datatable-gridlines"
          :rows="10"
          dataKey="id"
          :rowHover="true"
          v-model:filters="filters1"
          filterDisplay="menu"
          :loading="loading1"
          :filters="filters1"
          responsiveLayout="scroll"
          :globalFilterFields="['name']"
        >
          <template #header>
            <div class="flex justify-content-between flex-column sm:flex-row">
              <Button
                type="button"
                icon="pi pi-filter-slash"
                label="Clear"
                class="p-button-outlined mb-2"
                @click="clearFilter1()"
              />
              <span class="p-input-icon-left mb-2">
                <i class="pi pi-search" />
                <InputText
                  v-model="filters1['global'].value"
                  placeholder="Keyword Search"
                  style="width: 100%"
                />
              </span>
            </div>
          </template>
          <template #empty> No customers found. </template>
          <template #loading> Loading customers data. Please wait. </template>
          <Column field="name" header="Name" style="min-width: 12rem">
            <template #body="{ data }">
              {{ data.name }}
            </template>
            <template #filter="{ filterModel }">
              <InputText
                type="text"
                v-model="filterModel.value"
                class="p-column-filter"
                placeholder="Search by name"
              />
            </template>
          </Column>

          <Column
            field="verified"
            header="Verified"
            dataType="boolean"
            bodyClass="text-center"
            style="min-width: 8rem"
          >
            <template #body="{ data }">
              <i
                class="pi"
                :class="{
                  'text-green-500 pi-check-circle': data.verified,
                  'text-pink-500 pi-times-circle': !data.verified,
                }"
              ></i>
            </template>
            <template #filter="{ filterModel }">
              <TriStateCheckbox v-model="filterModel.value" />
            </template>
          </Column>
        </DataTable>
      </div>
    </div>
  </div>
</template>

<script>
import Controller from "../service/Commands";
import EventBus from "../AppEventBus";
import BibleService from "../service/BibleService";
import { FilterMatchMode, FilterOperator } from "primevue/api";

export default {
  data() {
    return {
      showScripture: false,
      showNumerics: false,
      customer1: null,
      filters1: null,
      filters2: {},
      loading1: true,
      loading2: true,
      idFrozen: false,
      products: null,
      expandedRows: [],
      dataviewValue: null,
      layout: "grid",
      sortKey: null,
      sortOrder: null,
      sortField: null,
      sortOptions: [
        { label: "Price High to Low", value: "!price" },
        { label: "Price Low to High", value: "price" },
      ],
    };
  },
  bibleService: null,
  created() {
    this.bibleService = new BibleService();
    this.initFilters1();
  },
  mounted() {
    this.loading2 = false;
    this.getBibleData = (event) => {
      const that = this;
      this.bibleService
        .getBible(
          this.$store.state.db,
          event.book.code,
          event.chapter.code,
          event.verse.code,
          event.thruVerse.code
        )
        .then(function (resp) {
          that.dataviewValue = resp.data;
          that.showScripture = true;
          that.showNumerics = true;
          let controller = new Controller(resp);
          controller.execute();
          that.customer1 = controller.commands;
          that.loading1 = false;
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        })
        .then(function () {
          // always executed
        });
    };

    EventBus.on("loadBible", (data) => {
      this.getBibleData(data);
    });
  },
  methods: {
    initFilters1() {
      this.filters1 = {
        global: { value: null, matchMode: FilterMatchMode.CONTAINS },
        name: {
          operator: FilterOperator.AND,
          constraints: [{ value: null, matchMode: FilterMatchMode.STARTS_WITH }],
        },
        verified: { value: null, matchMode: FilterMatchMode.EQUALS },
      };
    },
    clearFilter1() {
      this.initFilters1();
    },
    expandAll() {
      this.expandedRows = this.products.filter((p) => p.id);
    },
    collapseAll() {
      this.expandedRows = null;
    },
    onMenuToggle() {
      this.showScripture = !this.showScripture;
    },
    onMenuNumericsToggle() {
      this.showNumerics = !this.showNumerics;
    },
    onSortChange(event) {
      const value = event.value.value;
      const sortValue = event.value;

      if (value.indexOf("!") === 0) {
        this.sortOrder = -1;
        this.sortField = value.substring(1, value.length);
        this.sortKey = sortValue;
      } else {
        this.sortOrder = 1;
        this.sortField = value;
        this.sortKey = sortValue;
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import "../assets/demo/badges.scss";

::v-deep(.p-datatable-frozen-tbody) {
  font-weight: bold;
}

::v-deep(.p-datatable-scrollable .p-frozen-column) {
  font-weight: bold;
}
</style>
